footer {
    background-color: #5FBFF9;
    border-top: 5px solid #3E7CB1;
    position: fixed;
    bottom: 0;
    margin: 0;
    width: 100%;
    min-height: 30px;
}

#footerDiv {
    display: flex;
    justify-content: space-around;
}

#footerDiv > a {
    color: #FCFAFA;
    margin: 0 10px;
}